import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {Button, List, ListItem} from '@material-ui/core'

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
        position: 'absolute',
        overflow: 'auto',
        textAlign: 'center',
        height: '70%'
      },
    center: {
        textAlign: 'center',
        justifyContent: 'center',
    },
    itemList: {
        textAlign: 'center',
        ...theme.typography.button,
        backgroundColor: 'rgba(255,255,255,0.5)',
        padding: theme.spacing(1),
        maxWidth: '150px',
        minWidth: '150px',
        color: 'black',
        fontFamily: 'Niconne, cursive',
        fontWeight: '1000',
    },
    title: {
        margin: 0,
        paddingTop: 20,
        paddingBottom: 20,
        fontSize: '30px',
        textAlign: 'center',
        color: '#fcedd8',
        fontFamily: 'Niconne, cursive',
        fontWeight: '700',
        textShadow: '5px 5px 0px #eb452b',
    },
    text: {
        fontSize: '20px',
        textAlign: 'center',
        color: '#fcedd8',
        fontFamily: 'Niconne, cursive',
        fontWeight: '700',
        textShadow: '2px 2px 0px #017e7f',
    }
}));

const genders = [
    {
        name: "Agender",
        url: "https://gender.wikia.org/wiki/Agender",
    },
    {
        name: "Androgynous",
        url: "https://gender.wikia.org/wiki/Androgyny",
    },
    {
        name: "Bigender",
        url: "https://gender.wikia.org/wiki/Bigender",
    },
    {
        name: "Demigirl",
        url: "https://gender.wikia.org/wiki/Demigirl",
    },
    {
        name: "Demiboy",
        url: "https://gender.wikia.org/wiki/Demiboy",
    },
    {
        name: "Feminine",
        url: "https://gender.wikia.org/wiki/Femininity",
    },
    {
        name: "Femme",
        url: "https://gender.wikia.org/wiki/Femme",
    },
    {
        name: "Genderqueer",
        url: "https://gender.wikia.org/wiki/Genderqueer",
    },
    {
        name: "Genderfluid",
        url: "https://gender.wikia.org/wiki/Genderfluid",
    },
    {
        name: "Intersex",
        url: "https://gender.wikia.org/wiki/Intersex",
    },
    {
        name: "Man",
        url: "https://gender.wikia.org/wiki/Man",
    },
    {
        name: "Masculine",
        url: "https://gender.wikia.org/wiki/Masculinity",
    },
    {
        name: "Neutrois",
        url: "https://gender.wikia.org/wiki/Neutrois",
    },
    {
        name: "Nonbinary",
        url: "https://gender.wikia.org/wiki/Non-binary",
    },
    {
        name: "Other",
        url: "https://gender.wikia.org/wiki/Other_gender_identification",
    },
    {
        name: "Pangender",
        url: "https://gender.wikia.org/wiki/Pangender",
    },
    {
        name: "Third Gender",
        url: "https://gender.wikia.org/wiki/Third_Gender",
    },
    {
        name: "Woman",
        url: "https://gender.wikia.org/wiki/Woman",
    },
];

export default function GenderList() {
    const classes = useStyles();
    return (
        <div className="genderList">
            <div className={classes.center}>
                <h2 className={classes.title}>This is the actual list of genders.</h2>
                <p className={classes.text}>Click in order to get more information!</p>
                <List className={classes.root}>
                    {genders.map(function(d, idx){
                        return (<ListItem className={classes.center} key={idx}>
                                <Button className={classes.itemList} href={d.url} target="_blank">{d.name}</Button>
                            </ListItem>)
                    })}
                </List>
            </div>
        </div>
    )
}
