import './App.css';
import Portada from './components/Portada';
import GenderList from './components/GenderList';
import { HashRouter, Route, Switch } from 'react-router-dom';


function App() {
  return (
    <HashRouter>
      <div>
        <Switch>
          <Route path="/list">
            <GenderList />
          </Route>
          <Route path="/">
            <Portada />
          </Route>
        </Switch>
      </div>
    </HashRouter>
  );
}

export default App;
